/**
 * Customized version of bootstrap using variables from _variables.scss.
 * This file is loaded via separate loader thus allowing to use original bootstrap classes (e.g. .btn-default) through out the app.
 */
/*
 * Typography
 * ======================================================================== */
/**
 * Custom application mixins available through out the app
 */
.icon-list {
  margin-top: 1rem; }

.icon-list-item {
  height: 32px;
  font-size: 14px;
  line-height: 32px; }
  .icon-list-item > a {
    color: #495057;
    text-decoration: none; }
    .icon-list-item > a:hover {
      color: #495057;
      text-decoration: none; }
  .icon-list-item .glyphicon,
  .icon-list-item .fa,
  .icon-list-item .fi {
    width: 32px;
    margin-right: 10px; }
  .icon-list-item:hover .glyphicon,
  .icon-list-item:hover .fa,
  .icon-list-item:hover .fi {
    font-size: 28px;
    top: 2px; }
  .icon-list-item:hover .fa,
  .icon-list-item:hover .fi {
    vertical-align: -5px; }
  .icon-list-item:hover .glyphicon {
    vertical-align: -6px; }
