/**
 * Customized version of bootstrap using variables from _variables.scss.
 * This file is loaded via separate loader thus allowing to use original bootstrap classes (e.g. .btn-default) through out the app.
 */
/*
 * Typography
 * ======================================================================== */
/**
 * Custom application mixins available through out the app
 */
.status {
  display: block;
  position: relative;
  -webkit-box-shadow: 0 0 40px #f0f3ff;
          box-shadow: 0 0 40px #f0f3ff;
}
.statusBody::after {
  display: block;
  clear: both;
  content: "";
}
.status .db {
  border-radius: 5px;
}
.status .db:hover {
  /*background: #40516f;
    color: #fff;*/
  -webkit-box-shadow: 3px 3px 3px #dadada;
          box-shadow: 3px 3px 3px #dadada;
}
.status-icon {
  color: #fff;
  opacity: 0.5;
  font-size: 3em;
  height: 2em;
  line-height: 1.6 em;
  padding-right: 0.1em;
  padding-top: 0.3em;
  float: right;
}
.valor {
  color: #fff !important;
  padding-left: 1em;
  padding-top: 1em;
  float: left;
}
.valor-text {
  font-size: 1.5em;
  opacity: 0.9;
  font-weight: 400;
}
.small-valor-text {
  font-size: 0.6em;
  opacity: 0.9;
}
.panel {
  /*background-color: #5cb85c;*/
  -webkit-box-shadow: 2px 2px 10px #dadada;
          box-shadow: 2px 2px 10px #dadada;
}
.cart-title {
  width: auto;
  background: rgba(0, 0, 0, 0.35);
  border-radius: 5px;
}
.title-text {
  color: white;
  opacity: 0.6;
  padding-left: 1em;
  font-weight: bold;
}
.tooltip-icon {
  margin-right: 0.5em;
  font-size: 1em;
  color: #ffffff;
  float: right;
  opacity: 0.8;
}
