/**
 * Customized version of bootstrap using variables from _variables.scss.
 * This file is loaded via separate loader thus allowing to use original bootstrap classes (e.g. .btn-default) through out the app.
 */
/*
 * Typography
 * ======================================================================== */
/**
 * Custom application mixins available through out the app
 */
.chat {
  position: fixed;
  overflow: hidden;
  top: 0;
  bottom: 0;
  right: -250px;
  width: 250px;
  background-color: #fff;
  color: #aaa;
  -webkit-transition: right 0.3s ease-in-out;
  transition: right 0.3s ease-in-out;
}
@media (max-width: 991.98px) {
.chat {
      right: -275px;
      width: 275px;
}
}
.chat.chatOpen {
    right: 0;
}
.chat .chatSearch {
    position: relative;
}
.chat .chatSearch i {
      position: absolute;
      right: 0.75rem;
      top: 0.75rem;
      color: #495057;
}
.chat .chatHeader {
    position: absolute;
    width: 100%;
    top: 0;
    z-index: 3;
    padding: 10px;
    background-color: #fff;
}
.chat .chatHeader input {
      padding: 0.6rem 2.25rem 0.6rem 0.85rem;
      line-height: 1.5;
}
.chat .chatHeader .chatTitle {
      margin: 10px;
      text-transform: uppercase;
      font-size: 15px;
      font-weight: 400;
}
.chat .chatPanel {
    position: absolute;
    top: 0;
    bottom: 0;
    width: 100%;
}
.chat .chatPanel .list-group-item {
      border: 0;
      padding: 10px 20px;
      z-index: 1;
}
.chat .chatPanel .list-group-item .cirle {
        font-size: 11px;
        line-height: 37px;
        margin-left: auto;
}
.chat .chatPanel .navTitle {
      margin: 35px 10px 5px 20px;
      font-size: 14px;
}
.chat .chatPanel .navTitle:first-child {
      margin-top: 0;
}
.chat .chatPanel .messageSender {
      text-overflow: ellipsis;
}
.chat .chatPanel .messagePreview {
      margin: 0;
      width: 100px;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      font-size: 85%;
      color: #999;
}
.chat .chatContacts {
    padding-top: 105px;
    left: -100%;
    -webkit-transition: left 0.2s ease-in-out;
    transition: left 0.2s ease-in-out;
}
.chat .chatContacts .list-group {
      margin-top: 10px;
}
.chat .chatContacts .list-group .list-group-item {
        cursor: pointer;
}
.chat .chatContacts.chatMessageOpen {
      left: 0;
      overflow-y: auto;
}
.chat .chatContacts.chatMessageOpen::-webkit-scrollbar {
        height: 8px;
        width: 4px;
}
.chat .chatContacts.chatMessageOpen::-webkit-scrollbar-thumb {
        border: none;
        background-color: rgba(255, 255, 255, 0.3);
}
.chat .chatMessages {
    padding-top: 100px;
    right: -100%;
    -webkit-transition: right 0.2s ease-in-out;
    transition: right 0.2s ease-in-out;
    background-color: #f8f9fa;
}
.chat .chatMessages .list-group {
      position: absolute;
      top: 134px;
      bottom: 47px;
      width: 100%;
      padding-top: 0.5rem;
      overflow-y: auto;
}
.chat .chatMessages .list-group::-webkit-scrollbar {
        height: 8px;
        width: 4px;
}
.chat .chatMessages .list-group::-webkit-scrollbar-thumb {
        border: none;
        background-color: rgba(255, 255, 255, 0.3);
}
.chat .chatMessages .list-group .list-group-item {
        -webkit-box-align: start;
            -ms-flex-align: start;
                align-items: flex-start;
}
.chat .chatMessages .list-group .thumb-sm {
        float: left;
}
.chat .chatMessages .messageBody {
      position: relative;
      margin-left: 50px;
      padding: 10px;
      font-size: 13px;
      font-weight: 400;
      background-color: #e9ecef;
      color: #495057;
      border-radius: 0.25rem;
}
.chat .chatMessages .messageBody::before {
        right: 100%;
        top: 8px;
        content: '';
        height: 0;
        width: 0;
        position: absolute;
        border: 10px solid rgba(0, 0, 0, 0);
        border-right-color: #e9ecef;
}
.chat .chatMessages .fromMe {
      -webkit-box-orient: horizontal;
      -webkit-box-direction: reverse;
          -ms-flex-direction: row-reverse;
              flex-direction: row-reverse;
}
.chat .chatMessages .fromMe .thumb-sm {
        float: right;
}
.chat .chatMessages .fromMe .messageBody {
        margin-left: 0;
        margin-right: 50px;
        background-color: #ffc247;
        color: #3c484f;
}
.chat .chatMessages .fromMe .messageBody::before {
          right: auto;
          left: 100%;
          border-right-color: rgba(0, 0, 0, 0);
          border-left-color: #ffc247;
}
.chat .chatMessages .chatFooter {
      position: absolute;
      z-index: 1;
      bottom: 0;
      width: 100%;
      margin-bottom: 0;
      padding: 10px;
      background-color: #fff;
}
.chat .chatMessages.chatMessageOpen {
      right: 0;
}
.chat .chatMessages .messagesTitle {
      margin-bottom: 0;
}
.chat .chatMessages .messagesTitle a {
        display: block;
        margin-top: -7px;
        padding: 17px 16px;
        background-color: #fff;
}
.chat .chatSidebarUserGroup .list-group-item {
    -webkit-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
}
.chat .chatSidebarUserGroup .list-group-item.active {
    background: rgba(255, 255, 255, 0.1);
}
.chat .chatSidebarUserGroup .list-group-item.active h6 {
    color: #ffc247;
    font-weight: 700;
}
.chat .chatSidebarUserGroup .list-group-item:hover {
    background: #f8f9fa;
}
.chat .chatSidebarUserGroup .badge {
    margin: 8px 5px 0 0;
    padding: 3px 5px;
}
.chat .chatSidebarUserGroup .fa {
    margin-top: 11px;
}
.chat .messageItem:hover {
    background: transparent;
}
