/**
 * Customized version of bootstrap using variables from _variables.scss.
 * This file is loaded via separate loader thus allowing to use original bootstrap classes (e.g. .btn-default) through out the app.
 */
/*
 * Typography
 * ======================================================================== */
/**
 * Custom application mixins available through out the app
 */
.charts-overview {
  /*       NVD3      */
  /*         Flot        */
  /*   Easy Pie Chart    */
}
.charts-overview .nvtooltip {
    padding: 0.25rem 0.75rem;
    font-family: "Montserrat", sans-serif;
    font-size: 0.875rem;
    text-align: center;
}
.charts-overview .nvtooltip p {
      margin: 0;
      padding: 0;
}
.charts-overview .nvtooltip h3 {
      background: none;
      border-bottom: 0;
}
.charts-overview svg text {
    font: 300 0.875rem "Montserrat", sans-serif;
    fill: #495057;
}
.charts-overview svg .title {
    font: 700 1rem "Montserrat", sans-serif;
}
.charts-overview .nvd3.nv-noData {
    font-size: 1.25rem;
    font-weight: 700;
}
.charts-overview .nvd3 .nv-axis path.domain {
    stroke-opacity: 0;
}
.charts-overview .nv-controlsWrap .nv-legend-symbol {
    fill: #666 !important;
    stroke: #666 !important;
}
.charts-overview .chart-tooltip {
    position: fixed;
    padding: 0.25rem 0.75rem;
    border: 1px solid #798892;
    font-size: 0.9rem;
    background-color: #fff;
}
.charts-overview .easy-pie-chart {
    position: relative;
    display: inline-block;
    line-height: 120px;
    height: 120px;
    width: 120px;
    text-align: center;
    color: #798892;
}
.charts-overview .easy-pie-chart canvas {
      position: absolute;
      top: 0;
      left: 0;
}
