/**
 * Customized version of bootstrap using variables from _variables.scss.
 * This file is loaded via separate loader thus allowing to use original bootstrap classes (e.g. .btn-default) through out the app.
 */
/*
 * Typography
 * ======================================================================== */
/**
 * Custom application mixins available through out the app
 */
.title {
  margin-top: 0;
  color: #444;
}
.title::after {
    display: block;
    clear: both;
    content: "";
}
.widget {
  display: block;
  position: relative;
  margin-bottom: 30px;
  padding: 15px 20px;
  background: #fff;
  border-radius: 0.25rem;
  -webkit-box-shadow: 0 0 40px #f0f3ff;
          box-shadow: 0 0 40px #f0f3ff;
}
.widget > header {
    margin: -15px -20px;
    padding: 15px 20px;
}
.widget > header h1,
    .widget > header h2,
    .widget > header h3,
    .widget > header h4,
    .widget > header h5,
    .widget > header h6 {
      margin: 0;
}
.widget .loader {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
}
.widget .loader .spinner {
      position: absolute;
      top: 50%;
      width: 100%;
      margin-top: -10px;
      font-size: 20px;
      text-align: center;
}
.widget .widget-body.p-0 {
    margin: 15px -20px -15px;
}
.widget .widget-body.p-0 + footer {
      margin-top: 15px;
}
.widget.bg-transparent {
    -webkit-box-shadow: none;
            box-shadow: none;
}
.widgetBody::after {
  display: block;
  clear: both;
  content: "";
}
.widgetBody > footer {
  margin: 0.5rem -20px -15px;
  padding: 10px 20px;
}
.widgetControls + .widgetBody {
  margin-top: 15px;
}
.widgetControls,
.widget-controls {
  position: absolute;
  z-index: 1;
  top: 0;
  right: 0;
  padding: 14px;
  font-size: 0.875rem;
}
.widgetControls a,
  .widget-controls a {
    padding: 1px 4px;
    border-radius: 4px;
    color: rgba(0, 0, 0, 0.4);
    -webkit-transition: color 0.15s ease-in-out;
    transition: color 0.15s ease-in-out;
}
@media (prefers-reduced-motion: reduce) {
.widgetControls a,
      .widget-controls a {
        -webkit-transition: none;
        transition: none;
}
}
.widgetControls a:hover,
    .widget-controls a:hover {
      color: rgba(0, 0, 0, 0.1);
      text-decoration: none;
}
.widgetControls a .la,
    .widget-controls a .la {
      position: relative;
      top: 2px;
}
.widgetControls a .glyphicon,
    .widget-controls a .glyphicon {
      font-size: 0.7rem;
}
.inverse {
  top: 2px;
  position: relative;
  margin-left: 3px;
}
.inverse .glyphicon {
    vertical-align: baseline;
}
.widget-image {
  position: relative;
  overflow: hidden;
  margin: -15px -20px;
  border-radius: 0.3rem;
}
.widget-image > img {
    max-width: 100%;
    border-radius: 0.3rem 0.3rem 0 0;
    -webkit-transition: -webkit-transform 0.15s ease;
    transition: -webkit-transform 0.15s ease;
    transition: transform 0.15s ease;
    transition: transform 0.15s ease, -webkit-transform 0.15s ease;
}
.widget-image:hover > img {
    -webkit-transform: scale(1.1, 1.1);
            transform: scale(1.1, 1.1);
}
.widget-image .title {
    position: absolute;
    top: 0;
    left: 0;
    margin: 20px;
}
.widget-image .info {
    position: absolute;
    top: 0;
    right: 0;
    margin: 20px;
}
.widget-footer-bottom {
  position: absolute;
  bottom: 0;
  width: 100%;
}
.widget-sm {
  height: 230px;
}
.widget-md {
  height: 373px;
}
.widget-padding-md {
  padding: 15px 20px;
}
.widget-padding-lg {
  padding: 30px 40px;
}
.widget-body-container {
  position: relative;
  height: 100%;
}
.widget-top-overflow,
.widget-middle-overflow {
  position: relative;
  margin: 0 -20px;
}
.widget-top-overflow > img,
  .widget-middle-overflow > img {
    max-width: 100%;
}
.widget-top-overflow {
  margin-top: -15px;
  border-top-left-radius: 0.3rem;
  border-top-right-radius: 0.3rem;
  overflow: hidden;
}
.widget-top-overflow > img {
    border-top-left-radius: 0.3rem;
    border-top-right-radius: 0.3rem;
}
.widget-top-overflow > .btn-toolbar {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 1;
    margin-right: 20px;
}
@media (min-width: 768px) {
.widget-top-overflow > .btn-toolbar {
        top: auto;
        bottom: 0;
}
}
.widget-icon {
  opacity: 0.5;
  font-size: 42px;
  height: 60px;
  line-height: 45px;
  display: inline-block;
}
